<template>
  <nav>
    <v-app-bar flat app>
      <v-icon @click="reset($router, $route)" class="mr-3 pointer">mdi-feather</v-icon>
      <v-app-bar-title class="grey--text text-no-wrap">
        <span class="font-weight-light">AI &bull; </span>
        <span>Writing &bull; </span>
        <span class="font-weight-light">Studio</span>
      </v-app-bar-title>
      <v-spacer></v-spacer>
      <span class="caption">Last updated: 2021.8.15</span>
    </v-app-bar>
  </nav>
</template>

<script>
export default {
  methods: {
    reset(router, route) {
      if (route.name != "Home") {
        router.push("/");
      }
      this.$emit("resetContent");
    },
  },
};
</script>

